import React from 'react';
import TextField from '@material-ui/core/TextField';
import './login.css';
import Card from '@material-ui/core/Card';
import { makeStyles } from '@material-ui/core/styles';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/button'
import { Link, BrowserRouter as Router } from 'react-router-dom';
import { useState } from 'react';
const useStyles = makeStyles({
    root: {
      minWidth: 275,
      maxWidth:'fit-content',
      '& .MuiTextField-root': {
        width: 384,
        marginBottom:32
      },
      '& a':{
          color:'#0072ab !important',
      },
      '& p':{
        fontSize:14
      },
      '& .MuiCardContent-root':{
          padding:'16px 24px'
      }
    },
    wrapperDiv:{
    maxWidth:'fit-content',
    },
    wrapperHead:{
        maxWidth:'fit-content',
        marginBottom:16
},
buttonWrapper:{
marginBottom:16,
},
loginButton:{
    width:'100%'
}

  });
export default function Login(){
    const classes = useStyles();
    const [user, setUser] = useState({
      username:'',
      password: '',
  });

  const  handleChange = (ev)=>{
    const { name, value } = ev.target;
    setUser(user=>({...user,[name]:value}));
}

    return (
        <div className='login-background'>
        <Card className={classes.root}>
              <CardContent>
     <div>
     <Typography className={classes.wrapperHead} gutterBottom variant="h5" component="h2">
         Login
          </Typography>
       <div className={classes.wrapperDiv}>
        <TextField
        id="outlined-error-helper-text"
        label="User name"
        variant="outlined"
        name="username"
        value={user.username}
        onChange={handleChange}

      />
      </div><div  className={classes.wrapperDiv}>
           <TextField
        id="outlined-error-helper-text"
        label="Password"
        variant="outlined"
        type="password"
      />
     </div>
     </div>
    
    <div className={classes.buttonWrapper}>
    
      <Button variant="contained"  color="primary" className={classes.loginButton}>
  Log In
</Button>
</div>
<p><a>Forgot Password</a></p>
<p>No account?<Link to="/signup"> Create One</Link></p>
</CardContent>
      </Card>
      </div>
    );
}