import React from 'react';
import TextField from '@material-ui/core/TextField';
import './login.css';
import Card from '@material-ui/core/Card';
import { makeStyles } from '@material-ui/core/styles';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/button';
import {useState} from 'react';
import  { registerUser } from './http/api';

const useStyles = makeStyles({
    root: {
      minWidth: 275,
      maxWidth:'fit-content',
      '& .MuiTextField-root': {
        width: 384,
        marginBottom:32
      },
      '& a':{
          color:'#0072ab !important',
      },
      '& p':{
        fontSize:14
      },
      '& .MuiCardContent-root':{
          padding:'16px 24px'
      },
    },
    wrapperDiv:{
    maxWidth:'fit-content' 
    },
    wrapperHead:{
        maxWidth:'fit-content',
        marginBottom:16
},

buttonWrapper:{
marginBottom:16,
},
loginButton:{
    width:'100%'
}

  });

export default function Signup(){

    const classes = useStyles();
    const [user, setUser] = useState({
        username:'',
        email: '',
        password: '',
        reEnterPassword: '',
    });

  const  handleChange = (ev)=>{
        const { name, value } = ev.target;
        setUser(user=>({...user,[name]:value}));
    }

   const handleSubmit= (ev)=>{
        ev.preventDefault();
        registerUser(user);
   }

    return (
        <div className='login-background'>
        <Card className={classes.root}>
              <CardContent>
     <div>
     <Typography className={classes.wrapperHead} gutterBottom variant="h5" component="h2">
         Sign up
          </Typography>
          <div className={classes.wrapperDiv}>
        <TextField
        id="outlined-error-helper-text"
        label="User name"
        variant="outlined"
        value={user.username}
        name="username"
        onChange={handleChange}
      />

        <TextField
        id="outlined-error-helper-text"
        label="Email Id"
        variant="outlined"
        value={user.email}
        name="email"
        onChange={handleChange}
      />
      </div><div  className={classes.wrapperDiv}>
           <TextField
        id="outlined-error-helper-text"
        label="Password"
        variant="outlined"
        type="password"
        name="password"
        value={user.password}
        onChange={handleChange}
      />
     </div>
     <div  className={classes.wrapperDiv}>
           <TextField
        id="outlined-error-helper-text"
        label="Re-enter password"
        variant="outlined"
        type="password"
        name="reEnterPassword"
        value={user.reEnterPassword}
        onChange={handleChange}
      />
     </div>
     </div>
    
    <div className={classes.buttonWrapper}>
    
      <Button variant="contained" type="submit" onClick={handleSubmit}  color="primary" className={classes.loginButton}>
  Sign up
</Button>
</div>
</CardContent>
      </Card>
      </div>
    );
}